.button {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    padding: 8px 16px;
    margin: 10px auto;
    border-radius: 8px;
    background-color: #527c82;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
      font-weight: 600;
    text-transform: uppercase;

  }
  
  .button:hover,
  .button:focus {
    background-color:#e3f309cb;
  }